import React,{useState} from "react";
import "./App.css";
let listdata = [
    {name: "aj", title: "coolboy"},
    {name: "dj", title: "dangerboy"},
    {name: "pk", title : "pkboy"}
];
const remove = {
    color: "red",
    transform: "scale(.2)",
    height: "5px",
    backgroundColor: "blue",
    overflow: "hidden"    
};
const ulcss = { 
    position: "relative",
    listStyle: "none",
    margin: "0px",
    padding: "0px",
};
const Listapp = () => {
     const [list, updatelist] = useState(listdata);
     const [indxId, updateindxId] = useState();

     const handleDelet = (id, index) => {
         updateindxId(id) ;
         let timeout = setTimeout(() => {
            const updatedList = list.filter((data)=> data.name !== id);
            updatelist(updatedList);
         }, 1000); 
     }
     return <div>
         <ul style={ulcss}>
             {list.map((data, index) => <li className={ data.name == indxId ? "remove" : "" }>
                 <div>
                 <h2 className>{data.name}</h2>
                 <p>{data.title}</p>
                 <button onClick={() => {handleDelet(data.name, index)}}>Delet</button>
                 </div>
            </li> )}
         </ul>
     </div> 
     
}
export default Listapp;